import { Box, Grid } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement, useEffect, useState } from "react";
import PageTitle from "../common/PageTitle";
import * as Yup from "yup";
import ApplicationDetails from "./ApplicationDetails";
import { IRequest } from "../../../../estreeh-common-library/build/interfaces/IRequest";
import axios from "axios";
import { IResponse } from "../../../../estreeh-common-library/build/interfaces/IResponse";
import Worker2Card from "../workers/Worker2Card";
import { Formik } from "formik";
import UpdateRequestForm from "./UpdateRequestForm";
import { IApiResConfig } from "../../../../estreeh-common-library/build/interfaces/api/res/IApiResConfig";
import { IBackendFieldSelectOption } from "../../../../estreeh-common-library/build/interfaces/IBackendFieldSelectOption";
import { toast } from "react-toastify";
import { IStatus } from "../../../../estreeh-common-library/build/interfaces/IStatus";

interface Props extends RouteComponentProps {
  id?: string;
  config: IApiResConfig;
}

export default function UpdateRequestPage(props: Props): ReactElement {
  // const defaultStatus = Faker.random.arrayElement(WorkerWorkflowStatuses);
  const [loading, setLoading] = useState(true);
  const [request, setRequest] = useState<IRequest | null>(null);
  const headerVariant = "h6";
  let visibleStatus: IStatus[] = [];
  const status = request?.status;

  if (request !== null && status?.nextCodes !== undefined) {
    visibleStatus = props.config.statusCodesTwoYear.filter(
      (x) => status.nextCodes.includes(x.code) || x.code === request.statusCode
    );
  } else {
    visibleStatus = props.config.statusCodesTwoYear;
  }
  const dropdown = visibleStatus.map<IBackendFieldSelectOption>((x) => ({
    value: x.code,
    title: x.title,
    color: x.isReject
      ? "red"
      : x.nextCodes.length === 0
      ? "darkgreen"
      : undefined,
  }));

  useEffect(() => {
    if (loading) {
      axios
        .get<IResponse<IRequest>>(
          process.env.REACT_APP_API_ROOT + "/be/requests/" + props.id,
          { withCredentials: true }
        )
        .then((result) => {
          setLoading(false);
          setRequest(result.data.data);
        });
    }
  }, [loading, props.id]);

  return (
    <>
      <PageTitle title={"Update Request"}>
        Tracking # {request?.trackingCode}
      </PageTitle>

      {request !== null && request.workerTwoYear !== undefined && (
        <>
          <Box>
            <Grid container>
              <Grid item xs={12} md={6}>
                <ApplicationDetails
                  headerVariant={headerVariant}
                  applicationForm={request.applicationForm}
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <Worker2Card
                  headerVariant={headerVariant}
                  worker={request.workerTwoYear}
                />
              </Grid>
            </Grid>
          </Box>
          <Box>
            <Formik
              initialValues={request}
              validationSchema={Yup.object({
                // name: Yup.string().required("Required"),
                // experienceYears: Yup.number().required("Required"),
                // contractPeriod: Yup.number().required("Required"),
                // monthlySalary: Yup.number().required("Required"),
                // hidden: Yup.bool().required("Required"),

                // processTemplateId: Yup.string().required("Required"),
                statusCode: Yup.number().required("Required"),
              })}
              onSubmit={(formValues, { setSubmitting }) => {
                axios
                  .put<IResponse>(
                    process.env.REACT_APP_API_ROOT + "/be/requests",
                    formValues,
                    { withCredentials: true }
                  )
                  .then((result) => {
                    toast.success(result.data.message);
                    setSubmitting(false);
                    setLoading(true);
                  });
                // saveData(values, isNew)
                //   .then((result) => {
                //     completed({ result, navigateOut: isNew, setSubmitting });
                //     const _id = result.data.data._id;
                //     if (_id !== undefined) {
                //       if (formValues.uploadSmallPicture !== undefined) {
                //         uploadPicture({
                //           _id,
                //           file: formValues.uploadSmallPicture,
                //           folder: "2y/sm",
                //           navigateOut: isNew,
                //           result,
                //           setSubmitting,
                //         }).then((resultPic) => {
                //           // setSubmitting(false);
                //           completed({
                //             result: resultPic,
                //             navigateOut: isNew,
                //             setSubmitting,
                //           });
                //         });
                //       }

                //       if (formValues.uploadBigPicture !== undefined) {
                //         uploadPicture({
                //           _id,
                //           file: formValues.uploadBigPicture,
                //           folder: "2y/lg",
                //           navigateOut: isNew,
                //           result,
                //           setSubmitting,
                //         }).then((resultPic) => {
                //           // setSubmitting(false);
                //           completed({
                //             result: resultPic,
                //             navigateOut: isNew,
                //             setSubmitting,
                //           });
                //         });
                //       }
                //     }
                //   })
                //   .catch((reason) => {
                //     toast.error(reason.message);
                //     setSubmitting(false);
                //     // setSaving(false);
                //   });
              }}
            >
              {(formik) => (
                <UpdateRequestForm
                  formik={formik}
                  request={request}
                  disabled={loading}
                  dropdownOptions={dropdown}
                  config={props.config}
                />
              )}
            </Formik>
          </Box>
        </>
      )}
    </>
  );
}
