import { Grid } from "@material-ui/core";
import { FormikProps } from "formik";
import React, { ReactElement } from "react";
import { IRequest } from "../../../../estreeh-common-library/build/interfaces/IRequest";
import { IBackendField } from "../../../../estreeh-common-library/build/interfaces/IBackendField";
// import { WorkerWorkflowDropdown } from "../../config/WorkerWorkflowDropdown";
import BackendFieldSet from "../forms/BackendFieldSet";
import SubmitButton from "../forms/SubmitButton";
import { IBackendFieldSelectOption } from "../../../../estreeh-common-library/build/interfaces/IBackendFieldSelectOption";
import HistoryItem from "./HistoryItem";
import { IApiResConfig } from "../../../../estreeh-common-library/build/interfaces/api/res/IApiResConfig";

interface Props {
  formik: FormikProps<any>;
  disabled: boolean;
  request: IRequest;
  dropdownOptions: IBackendFieldSelectOption[];
  config: IApiResConfig;
}

export default function UpdateRequestForm({
  formik,
  disabled,
  request,
  dropdownOptions,
  config,
}: Props): ReactElement {
  const fields: IBackendField<IRequest>[] = [
    {
      name: "statusCode",
      variant: "select",
      // defaultValue: defaultStatus,
      selectOptions: dropdownOptions,
    },
    {
      name: "statusRemarksNew",
      variant: "textarea",
    },
    // {
    //   name: "notify",
    //   label: "Notify customer of status update.",
    //   variant: "checkbox",
    //   defaultValue: true,
    // },
    // {
    //   name: "showHistory",
    //   label: "Record status change in the customer request's history.",
    //   variant: "checkbox",
    //   defaultValue: true,
    // },
    // {
    //   name: "hide",
    //   label: "Hide this worker from other customers",
    //   variant: "checkbox",
    // },
  ];

  return (
    <form onSubmit={formik.handleSubmit}>
      <Grid container spacing={2}>
        <Grid item md={6} xs={12}>
          <BackendFieldSet
            header="Agency Actions"
            fields={fields}
            md={12}
            formik={formik}
          />
        </Grid>
        <Grid item md={6} xs={12}>
          <BackendFieldSet header="History" md={12}>
            {request.statusHistory.map((history) => (
              <HistoryItem
                history={history}
                key={history.created.toString() + history.createdByUserId}
                config={config}
              />
            ))}
          </BackendFieldSet>
        </Grid>
      </Grid>
      <SubmitButton
        title="Save"
        type="submit"
        disabled={disabled || formik.isSubmitting}
      />
    </form>
  );
}
