import { Box, Grid, Typography } from "@material-ui/core";
import { Variant } from "@material-ui/core/styles/createTypography";
import React, { ReactElement } from "react";
import { IApplicationForm } from "../../../../estreeh-common-library/build/interfaces/IApplicationForm";
import LabelValueLine from "../common/LabelValueLine";

interface Props {
  applicationForm: IApplicationForm;
  headerVariant: "inherit" | Variant;
}

export default function ApplicationDetails({
  applicationForm: a,
  headerVariant: hVar,
}: Props): ReactElement {
  return (
    <>
      <Typography variant={hVar}>Application Details</Typography>
      <Box my={2}>
        <Grid container>
          <LabelValueLine title="Full Name">{a.fullName}</LabelValueLine>
          <LabelValueLine title="Mobile">{a.mobileNumber}</LabelValueLine>
          <LabelValueLine title="Other Mobile">
            {a.otherMobileNumber}
          </LabelValueLine>
          <LabelValueLine title="Special Notes">
            {a.specialNotes}
          </LabelValueLine>
          {/* <LabelValueLine title="Email">{a.email}</LabelValueLine> */}
        </Grid>
      </Box>
    </>
  );
}
