import { Box, Grid } from "@material-ui/core";
import { Skeleton } from "@material-ui/lab";
import { CSSProperties } from "@material-ui/styles";
import React, { ReactElement } from "react";
import Branding from "../../config/Branding";

interface Props {
  title?: string;
  value?: number;
}

export default function DashboardCard(props: Props): ReactElement {
  const styles: CSSProperties = {
    marginLeft: "auto",
    marginRight: "auto",
  };
  return (
    <Grid item md={3} xs={12}>
      <Box
        bgcolor={Branding.lighter}
        p={3}
        borderRadius={Branding.borderRadius.niceRadius}
        boxShadow={Branding.shadow.niceShadow}
        textAlign="center"
      >
        <Box>
          <Box
            style={{
              textTransform: "uppercase",
              height: "3rem",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            {props.title}
          </Box>
        </Box>
        <Box py={1} fontSize="3.5rem">
          {props.value === undefined && (
            <Skeleton
              variant="text"
              width={120}
              animation="wave"
              style={styles}
            />
          )}
          {props.value !== undefined && <Box>{props.value}</Box>}
        </Box>
        {/* <Box>
          <Skeleton variant="text" animation="wave" style={styles} />
        </Box> */}
      </Box>
    </Grid>
  );
}
