import { Avatar, Box, Grid, Typography } from "@material-ui/core";
import { Variant } from "@material-ui/core/styles/createTypography";
import React, { ReactElement } from "react";
import IWorkerTwoYear from "../../../../estreeh-common-library/build/interfaces/IWorkerTwoYear";
import { S3Folder } from "../../../../estreeh-common-library/build/types/S3Folder";
import LabelValueLine from "../common/LabelValueLine";

interface Props {
  worker: IWorkerTwoYear;
  headerVariant: "inherit" | Variant;
}

export default function Worker2Card({ worker: w, headerVariant: hVar }: Props): ReactElement {
  const folder: S3Folder = "2y/sm";
  return (
    <>
      <Typography variant={hVar}>Requested Worker</Typography>
      <Box my={2}>
        <Grid container spacing={2}>
          <Grid item xs={4}>
            <Avatar
              src={`${process.env.REACT_APP_IMAGES_ROOT}/${folder}/${w.id}.${w.smallPictureFileExt}`}
              style={{
                // width: 160,
                width: "100%",
                minHeight: 160,
                alignSelf: "center",
              }}
              variant="rounded"
            />
          </Grid>
          <Grid item xs={8}>
            <Grid container>
              <LabelValueLine title="Agency">{w.agency?.title}</LabelValueLine>
              <LabelValueLine title="Ref #">{w.referenceId}</LabelValueLine>
              <LabelValueLine title="Name">{w.name}</LabelValueLine>
              <LabelValueLine title="Nationality">
                {w.nationality}
              </LabelValueLine>
              <LabelValueLine title="Job Title">{w.jobTitle}</LabelValueLine>
              <LabelValueLine title="Salary">
                BHD {w.monthlySalary} / Month
              </LabelValueLine>
              <LabelValueLine title="Processing Fees">
                BHD {w.processFees}
              </LabelValueLine>
              <LabelValueLine title="Contract Period">
                {w.contractPeriod} Years
              </LabelValueLine>
            </Grid>
          </Grid>
        </Grid>
      </Box>
    </>
  );
}
