import { Box, Grid } from "@material-ui/core";
import dateFormat from "dateformat";
import React, { ReactElement } from "react";
import { IRequestStatusHistory } from "../../../../estreeh-common-library/build/interfaces/IRequestStatusHistory";
import { IApiResConfig } from "../../../../estreeh-common-library/build/interfaces/api/res/IApiResConfig";
import Branding from "../../config/Branding";
import SmallText from "../common/SmallText";

interface Props {
  config: IApiResConfig;
  history: IRequestStatusHistory;
}

export default function HistoryItem({ history, config }: Props): ReactElement {
  const status = config.statusCodesTwoYear.find(
    (x) => x.code === history.statusCode
  );
  return (
    <Box
      boxShadow={"0 0 9px " + Branding.primary + "44"}
      my={1}
      py={1}
      px={2}
      borderRadius={16}
    >
      <SmallText>
        <Grid container spacing={1}>
          <Grid item xs={12} md={8}>
            <Box color="cornflowerblue" fontWeight="bold">
              {status?.title}
            </Box>
          </Grid>
          <Grid item xs={12} md={4}>
            <Box textAlign="right" fontStyle="italic">
              {dateFormat(history.created, "d mmm yyyy, h:MM tt")}
            </Box>
          </Grid>
          <Grid item xs={12} md={6}>
            {history.statusRemarks}
          </Grid>
          <Grid item xs={12} md={6}>
            <Box style={{ color: "yellowgreen", textAlign:"right" }}  fontStyle="italic">{history.createdBy}</Box>
          </Grid>
        </Grid>
      </SmallText>
    </Box>
  );
}
