import { Avatar, Box, Button, ButtonGroup } from "@material-ui/core";
import { DataGrid, GridColumns } from "@material-ui/data-grid";
import { RouteComponentProps } from "@reach/router";
import { ReactElement, useEffect } from "react";
import IWorkerTwoYear from "../../../../estreeh-common-library/build/interfaces/IWorkerTwoYear";
import { Edit } from "@material-ui/icons";
import PageTitle from "../common/PageTitle";
import { useState } from "react";
import { IRequest } from "../../../../estreeh-common-library/build/interfaces/IRequest";
import { IResponse } from "../../../../estreeh-common-library/build/interfaces/IResponse";
import axios from "axios";
import dateFormat from "dateformat";
import { S3Folder } from "../../../../estreeh-common-library/build/types/S3Folder";
import { IApiResConfig } from "../../../../estreeh-common-library/build/interfaces/api/res/IApiResConfig";

interface Props extends RouteComponentProps {
  config: IApiResConfig;}

export default function MyRequestsPage(props: Props): ReactElement {
  //TODO: "New requests" page or filter
  //TODO: "Pending for Action" page or filter

  const [requests, setRequests] = useState<IRequest[]>([]);

  const columns: GridColumns = [
    {
      field: "created",
      headerName: "Date",
      sortable: true,
      width: 140,
      valueFormatter: (params) => {
        return dateFormat(params.row.created, "dd mmm yy");
      },
    },
    {
      field: "avatar",
      headerName: "Pic",
      disableColumnMenu: true,
      renderCell: (params) => {
        const folder: S3Folder = "2y/sm";
        const worker: IWorkerTwoYear = params.row.workerTwoYear;
        const src =
          "/" + folder + "/" + worker._id + "." + worker.smallPictureFileExt;
        return (
          <Box>
            <Avatar src={process.env.REACT_APP_IMAGES_ROOT + src} />
          </Box>
        );
      },
    },
    {
      field: "Tracking",
      sortable: false,
      width: 130,
      valueGetter: (params) => {
        return params.row.trackingCode;
      },
    },
    {
      field: "Worker Name",
      sortable: true,
      width: 200,
      valueGetter: (params) => {
        const w: IWorkerTwoYear = params.row.workerTwoYear;
        return w.name;
      },
    },
    {
      field: "statusCode",
      headerName: "Status",
      sortable: true,
      width: 180,
      valueGetter: (params) => {
        return props.config.statusCodesTwoYear.find((x) => x.code === params.row.statusCode)?.title
      },
    },
    {
      field: "fullName",
      headerName: "Applicant Name",
      sortable: true,
      width: 250,
    },

    {
      field: "mobileNumber",
      headerName: "Mobile",
      sortable: true,
      width: 200,
    },
    {
      sortable: false,
      disableColumnMenu: true,
      field: "actions",
      headerName: "Actions",
      width: 140,
      renderCell: (params) => (
        <Box textAlign="center">
          <ButtonGroup color="primary" variant="outlined">
            <Button
              size="small"
              variant="outlined"
              color="primary"
              onClick={() => {
                console.log(params.row);
                if (props.navigate) props.navigate(`update/${params.row.id}`);
              }}
            >
              <Edit fontSize="small" />
            </Button>
            {/* <Button size="small">
              <ListAlt fontSize="small" />
            </Button> */}
          </ButtonGroup>
        </Box>
      ),
    },

    // {
    //   field: "specialNotes",
    //   sortable: true,
    //   width: 200,
    // },
  ];

  useEffect(() => {
    console.log("Preparing to get Requests");
    axios
      .get<IResponse<IRequest[]>>(
        process.env.REACT_APP_API_ROOT + "/be/requests",
        { withCredentials: true }
      )
      .then((requests) => {
        console.log(`Got ${requests.data.data.length} requests`);
        setRequests(requests.data.data);
      });
  }, [setRequests]);
  // const applications: IApplicationForm[] = ApplicationFormFactory.buildList(14);

  return (
    <Box>
      <PageTitle title="My Requests" />
      <Box height={1200}>
        <DataGrid
          disableSelectionOnClick={true}
          rows={requests.map((v) => {
            const a = {
              ...v.applicationForm,
              trackingCode: v.trackingCode,
              // requestId: v.id,
              id: v._id,
              workerTwoYear: v.workerTwoYear,
              statusCode: v.statusCode,
              created: v.created,
            };
            return a;
          })}
          columns={columns}
          pageSize={15}
          // checkboxSelection
        />
      </Box>
    </Box>
  );
}
