import React, { ReactElement, useEffect, useState } from "react";
import {
  Box,
  FormGroup,
  Checkbox,
  FormControlLabel,
  Divider,
  Grid,
  Avatar,
} from "@material-ui/core";
import { Alert } from "@material-ui/lab";
import { RouteComponentProps } from "@reach/router";
import PageTitle from "../common/PageTitle";
import SubmitButton from "../forms/SubmitButton";
import { IBackendField } from "../../../../estreeh-common-library/build/interfaces/IBackendField";
// import { AreasDropdown } from "../../config/AreasDropdown";
import Faker from "faker";
import BackendFieldSet from "../forms/BackendFieldSet";
import { nationalities } from "../../config/Nationalities";
import { Formik } from "formik";
import * as Yup from "yup";
import IAgency from "../../../../estreeh-common-library/build/interfaces/IAgency";
import axios from "axios";
import { IResponse } from "../../../../estreeh-common-library/build/interfaces/IResponse";
import { S3Folder } from "../../../../estreeh-common-library/build/types/S3Folder";
import { toast } from "react-toastify";

interface Props extends RouteComponentProps {}

export default function AgencySettingsPage(props: Props): ReactElement {
  const [agency, setAgency] = useState<Partial<IAgency>>({});
  const [loading, setLoading] = useState(true);
  const folder: S3Folder = "agency/sm";

  useEffect(() => {
    if (loading) {
      axios
        .get<IResponse<IAgency>>(
          process.env.REACT_APP_API_ROOT + "/be/agency",
          { withCredentials: true }
        )
        .then((result) => {
          setAgency(result.data.data);
          setLoading(false);
        });
    }
  }, [loading, setLoading]);

  const fields: IBackendField<IAgency>[] = [
    {
      name: "title",
      label: "Agency Name (English)",
      readOnly: true,
      // defaultValue: agencyName.name,
    },
    {
      name: "titleAr",
      label: "Agency Name (Arabic)",
      readOnly: true,
      // defaultValue: agencyName.nameAr,
    },
    {
      name: "cr",
      readOnly: true,
      defaultValue:
        Faker.datatype.number({ min: 10000, max: 99999 }) +
        "-" +
        Faker.datatype.number({ min: 0, max: 9 }),
    },
    // {
    //   name: "area",
    //   label: "Agency Area",
    //   // variant: "select",
    //   readOnly: true,
    //   defaultValue: CamelToSentence(
    //     Faker.random.arrayElement(AreasDropdown.map((a) => a.value))
    //   ),
    //   // selectOptions: AreasDropdown,
    // },
    {
      name: "telephone",
    },
    {
      name: "mobile",
    },
    {
      name: "email",
    },
    {
      name: "openingTimes",
      md: 12,
      variant: "textarea",
      textareaLines: 4,
    },
  ];

  const pictureFields: IBackendField<IAgency>[] = [
    {
      name: "uploadLogo",
      // label: "Change Logo",
      variant: "file",
      buttonIcon: "photo_camera",
    },
  ];

  // const unitField: IBackendField = {
  //   name: "unit",
  //   variant: "select",
  //   selectOptions: [{ value: "weeks" }, { value: "months" }],
  // };

  const termsLines = 11;

  const termsFields: IBackendField[] = [
    // {
    //   name: "General T&C",
    //   variant: "textarea",
    // },
    // {
    //   name: "General Terms & Conditions",
    //   variant: "textarea",
    //   textareaLines:lines,
    // },
    // {
    //   name: "Contract Terms & Conditions",
    //   variant: "textarea",
    //   textareaLines:lines,
    //   md:12,
    // },
    {
      name: "Local Transfer Worker Terms & Conditions",
      variant: "textarea",
      textareaLines: termsLines,
    },
    // {
    //   name: "Payments Terms & Conditions",
    //   variant: "textarea",
    //   textareaLines:lines,
    // },
    // {
    //   name: "Rental Workers T&C",
    //   variant: "textarea",
    // },
  ];

  // TODO: TO use this in future

  const nationalitiesComponent = (
    <Box>
      <PageTitle title="Nationalities" />
      <BackendFieldSet xs={12}>
        <FormGroup row>
          {nationalities.map((nationality) => (
            <FormControlLabel
              control={
                <Checkbox name={"nationality_" + nationality} color="primary" />
              }
              label={nationality}
              key={nationality}
            />
          ))}
        </FormGroup>
      </BackendFieldSet>
    </Box>
  );

  const div = (
    <Box my={4}>
      <Divider />
    </Box>
  );

  function completed(setSubmitting: (isSubmitting: boolean) => void) {
    setSubmitting(false);
    if (props.navigate) props.navigate("success");
  }

  function uploadPicture(
    file: any,
    setSubmitting: (isSubmitting: boolean) => void
  ) {
    let formData = new FormData();
    // let folder:S3Folder = ""
    // formData.append("_id", v._id);
    // formData.append("folder", )
    formData.append("file", file);
    axios
      .request<IResponse>({
        method: "PUT",
        url: process.env.REACT_APP_API_ROOT + "/be/agency/picture",
        data: formData,
        withCredentials: true,
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then((result) => {
        toast.success(result.data.message);
        completed(setSubmitting);
      });
  }

  /* ----------------------------------------
  TODO: IDEAS to be in this page:

  - On/Off Payment methods support:
  --- Cash
  --- VISA
  --- Benefit
  --- Debit Card

  -----------------------------------------*/

  return (
    <>
      {!loading && (
        <Formik
          initialValues={agency}
          validationSchema={Yup.object({
            // name: Yup.string().required("Required"),
            // processTemplateId: Yup.string().required("Required"),
          })}
          onSubmit={(formValues, { setSubmitting }) => {
            setSubmitting(true);
            const method = "PUT";
            const url = process.env.REACT_APP_API_ROOT + "/be/agency";
            axios
              .request<IResponse<IAgency>>({
                method,
                url,
                data: formValues,
                withCredentials: true,
              })
              .then((result) => {
                toast.success(result.data.message);
                if (formValues.uploadLogo !== undefined) {
                  uploadPicture(formValues.uploadLogo, setSubmitting);
                } else {
                  completed(setSubmitting);
                }
              })
              .catch((reason) => {
                toast.error(reason.message);
                setSubmitting(false);
              });
          }}
        >
          {(formik) => (
            <form onSubmit={formik.handleSubmit}>
              <React.Fragment>
                <PageTitle title="Agency Settings" />
                <BackendFieldSet
                  header="Agency Profile"
                  fields={fields}
                  md={6}
                  formik={formik}
                />
                <BackendFieldSet header="Images" xs={12}>
                  <Grid container>
                    <Grid item xs={4}>
                      <Avatar
                        src={
                          process.env.REACT_APP_IMAGES_ROOT +
                          "/" +
                          folder +
                          "/" +
                          agency._id +
                          "." +
                          agency.logoFileExt
                        }
                        variant="rounded"
                        style={{
                          width: 160,
                          height: 160,
                        }}
                      />
                    </Grid>
                  </Grid>
                </BackendFieldSet>
                <BackendFieldSet
                  xs={12}
                  fields={pictureFields}
                  formik={formik}
                />
              </React.Fragment>
              {div}
              <>
                {false && nationalitiesComponent}
                <PageTitle title="Terms & Conditions" />
                <BackendFieldSet fields={termsFields} md={12} formik={formik} />
              </>
              {div}
              <React.Fragment>
                <PageTitle title="Payment Methods" />
                <Box>
                  <Alert severity="info">
                    Currently only Cash option is available. More options will
                    be available in near future.
                  </Alert>
                </Box>
                <SubmitButton
                  errors={formik.errors}
                  type="submit"
                  title="save"
                  disabled={formik.isSubmitting}
                />
              </React.Fragment>
            </form>
          )}
        </Formik>
      )}
    </>
  );
}
